.base-learn {
    display: flex;
    flex-direction: column;

    margin: 0 20px;
 
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0,0,0,0.5);

}

.base-learn__image {
    margin: 0;
    padding: 0;
    height: 200px;

    border-top: transparent;
    border-radius: 10px 10px 0 0;

    
    background-image: url(../../img/face.jpg);
    background-position: center 65%;
    background-size: cover;
    background-repeat: no-repeat;
}   

.base-learn__title {
    font-style: 20px;
    line-height: 24px;
    font-weight: 700;
    text-align: center;   

    margin: 20px 0;
    padding: 0;
    width: 100%;
}

.base-learn-list {
    list-style: none;
    margin: 0;
    padding: 0 25px;  
    padding-left: 30px;
}

.base-learn-list__item {
    margin: 10px 0;
    position: relative;
    

    &::after {
        content: '';

        position: absolute;
        bottom: 0;
        left: 0;

        width: 100%;
        height: 1px;
        background-color: #000000;
        opacity: 0.2; 
    }
}

.base-learn__text {
    font-family: 'Ubuntu',Arial,sans-serif;
    font-weight: 300;
    color: #000000;
    text-align: center;

    margin: 25px; 
}

.base-learn__price {
    display: block;
    box-sizing: border-box;

    width: 280px;
    height: 50px;
    margin: 50px auto;
    padding: 15px;

    font-size: 14px;
    line-height: 20px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;  

    color: #ffffff;
    background-color: #222222;
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;

    cursor: pointer;
}
